/* ACCESSIBILITY HELPERS
---------------------------------------------------------------------------*/
.webaim-hidden{
	@include set-webaim-hidden();
}

.skip-link{
  @include set-webaim-hidden();
  z-index:1000;
}

.skip-link:focus {
  @include set-webaim-hidden-false();
  position:fixed;
  left: $gutter-width;
  top: $gutter-width;   
  background: white;
}