/* BUTTON COMPONENT
---------------------------------------------------------------------------*/
button {
  border: none;
  padding: 0;
  appearance: none;
  font-size: 1em;
  font-weight: 400;
  line-height: 1.625;
  cursor: pointer;
}

.button {
  appearance: none;
  padding: 10px 25px;
  cursor: pointer;
  transition: all 0.2s;
  display: inline-block;
  font-family: sans-serif;
  text-decoration: none;
  border-radius: 0;
  border: none;
  margin: 5px;
  margin-left: 0;
  background: white;
  color: #47476b;
  font-size: 1em;
  font-weight: 700;
  line-height: 1.625;
  box-shadow: 0 0 5px #CCCCCC;
}

.button:active {
  transform: translate(1px, 1px);
  box-shadow: inset 0px 0px 10px #CCCCCC;
}

.button:last-child {
  margin-bottom: 0;
}

.button--primary {
  background: #b30077;
  color: white;
}

.button--primary:hover, .button--primary:focus {
  background: #e60099;
}

.button--secondary {
  background: #47476b;
  color: white;
}

.button--secondary:hover, .button--secondary:focus {
  background: #5b5b8a;
}

.button--text {
  padding: 0;
  background: transparent;
  box-shadow: none;
  color: #b30077;
  display: inline;
  padding: 0;
  margin: 0;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: border 0.2s ease-in-out;
}

.button--text:hover, .button--text:focus {
  border-bottom: 1px solid #b30077;
}

.button--text:active {
  box-shadow: none;
}

.button--dropdown {
  width: 100%;
  display: block;
  border: 1px solid #CCCCCC;
}

.button--dropdown + .dropdown__content {
  border-top: none;
}

.button--dropdown i {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  transition: transform 0.2s;
}

.button--dropdown.open i {
  transform: rotate(180deg) translateY(50%);
}

.button--small {
  padding: 5px 15px;
  font-size: 0.875em;
  font-weight: 700;
  line-height: 1.71429;
}

.button--large {
