/* TOOLTIP COMPONENTS
---------------------------------------------------------------------------*/
.tooltip {
  display: none;
  background: white;
  width: 300px;
  padding: 10px;
  position: absolute;
  bottom: 100%;
  left: 50%;
  margin-bottom: 10px;
  transform: translateX(-50%);
  z-index: 10;
  box-shadow: 0 0 5px #CCCCCC;
}

.tooltip:before, .tooltip:after {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.tooltip--top:before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-left: 9.5px solid transparent;
  border-right: 9.5px solid transparent;
  border-top: 9.5px solid #CCCCCC;
  bottom: -9.5px;
}

.tooltip--top:after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 8px solid white;
  bottom: -8px;
}

.tooltip--bottom {
  bottom: auto;
  top: 100%;
  margin-bottom: 0;
  margin-top: 10px;
}

.tooltip--bottom:before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-left: 9.5px solid transparent;
  border-right: 9.5px solid transparent;
  border-bottom: 9.5px solid #CCCCCC;
  top: -9.5px;
}

.tooltip--bottom:after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid white;
  top: -8px;
}

.tooltip--left:before, .tooltip--left:after, .tooltip--right:before, .tooltip--right:after {
  top: 50%;
  transform: translateY(-50%);
}

.tooltip--left {
  bottom: auto;
  left: auto;
  right: 100%;
  top: 50%;
  margin-bottom: 0;
  margin-right: 10px;
  transform: translateY(-50%);
}

.tooltip--left:before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-top: 9.5px solid transparent;
  border-bottom: 9.5px solid transparent;
  border-left: 9.5px solid #CCCCCC;
  bottom: auto;
  left: auto;
  right: -9.5px;
  transform: translateY(-50%);
}

.tooltip--left:after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-top: 9.5px solid transparent;
  border-bottom: 9.5px solid transparent;
  border-left: 9.5px solid white;
  bottom: auto;
  left: auto;
  right: -8px;
  transform: translateY(-50%);
}

.tooltip--right {
  bottom: auto;
  left: 100%;
  top: 50%;
  margin-bottom: 0;
  margin-left: 10px;
  transform: translateY(-50%);
}

.tooltip--right:before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-top: 9.5px solid transparent;
  border-bottom: 9.5px solid transparent;
  border-right: 9.5px solid #CCCCCC;
  bottom: auto;
  left: -9.5px;
  transform: translateY(-50%);
}

.tooltip--right:after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-top: 9.5px solid transparent;
  border-bottom: 9.5px solid transparent;
  border-right: 9.5px solid white;
  bottom: auto;
  left: -8px;
  transform: translateY(-50%);
}

.tooltip__toggle {
  position: relative;
  overflow: visible;
}

.tooltip__toggle:hover .tooltip, .tooltip__toggle:focus .tooltip {
  display: inline-block;
}

/* FORM COMPONENTS
---------------------------------------------------------------------------*/
form {
  margin-bottom: 30px;
}

form:last-child {
  margin-bottom: 0;
}

.form__group {
  margin-bottom: 30px;
}

.form__group:last-child {
  margin-bottom: 0;
}

.form__group--has-icon {
  position: relative;
}

.form__group--has-icon .form__input {
  padding-left: 45px;
}

.form__group--has-icon i {
  position: absolute;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
}

.form__group--inline-button {
  position: relative;
}

.form__group--inline-button .form__input {
  padding-right: 115px;
}

.form__group--inline-button .button {
  margin: 0;
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
}

.form__group--inline-label {
  display: flex;
  align-items: center;
}

.form__group--inline-label .form__label {
  display: inline-block;
  white-space: nowrap;
  margin: 0;
  margin-right: 15px;
}

.form__group--inline-label .form__input {
  display: inline-block;
  width: 100%;
}

.form__label {
  display: block;
  margin-bottom: 5px;
  font-weight: 600;
}

.form__input {
  width: 100%;
  max-width: 100%;
  appearance: none;
  border: none;
  border-radius: 0;
  background: white;
  color: #47476b;
  font-family: sans-serif;
  padding: 10px 15px;
  margin: 0;
  text-align: left;
  border: 1px solid #CCCCCC;
  box-shadow: 0 0 5px #CCCCCC;
  font-size: 1em;
  font-weight: 400;
  line-height: 1.625;
}

.form__helper {
  font-size: 0.9em;
}

.form__select-wrapper {
  position: relative;
  display: block;
  width: 100%;
}

.form__select-wrapper .form__input {
  padding-right: 30px;
}

.form__select-wrapper .form__input + i {
  pointer-events: none;
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  color: black;
}

.form__radio, .form__checkbox {
  display: block;
  padding-left: 30px;
  position: relative;
  margin-bottom: 10px;
}

.form__radio input, .form__checkbox input {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.form__radio input:checked + .form__checkbox-label .form__checkbox-icon,
.form__radio input:checked + .form__checkbox-label .form__radio-icon,
.form__radio input:checked + .form__radio-label .form__checkbox-icon,
.form__radio input:checked + .form__radio-label .form__radio-icon, .form__checkbox input:checked + .form__checkbox-label .form__checkbox-icon,
.form__checkbox input:checked + .form__checkbox-label .form__radio-icon,
.form__checkbox input:checked + .form__radio-label .form__checkbox-icon,
.form__checkbox input:checked + .form__radio-label .form__radio-icon {
  display: block;
}

.form__radio input:focus + .form__checkbox-label:before,
.form__radio input:focus + .form__radio-label:before, .form__checkbox input:focus + .form__checkbox-label:before,
.form__checkbox input:focus + .form__radio-label:before {
  box-shadow: 0 0 5px #CCCCCC;
}

.form__radio-label:before, .form__checkbox-label:before {
  content: '';
  height: 15px;
  width: 15px;
  background: white;
  border: 1px solid #CCCCCC;
  position: absolute;
  left: 0;
  top: 0;
  transition: background 0.2s ease-in-out;
  cursor: pointer;
}

.form__radio-icon, .form__checkbox-icon {
  position: absolute;
  left: 4px;
  top: 2px;
  cursor: pointer;
  font-size: 0.8125em;
  font-weight: 400;
  line-height: 1.76923;
  color: #47476b;
  display: none;
}

.form__radio-label:before {
  border-radius: 100%;
}

.form__radio-icon {
  height: 9px;
  width: 9px;
  border-radius: 100%;
  background: #47476b;
  display: none;
  position: absolute;
  left: 4px;
  top: 4px;
}

.form--inline .form__group, .form--inline .form__label, .form--inline .form__input {
  display: inline-block;
}

.form--inline .form__group {
  margin-right: 30px;
}

.form--inline .form__group:last-of-type {
  margin-right: 0;
}

.form--inline .form__label {
  margin-right: 10px;
}

.form__switch {
  margin-bottom: 10px;
}

.form__switch:last-of-type {
  margin-bottom: 0;
}

.form__switch .form__switch-toggle {
  display: inline-block;
  cursor: pointer;
  padding: 8px 15px;
  border-radius: 50px;
  border: 1px solid #CCCCCC;
  font-weight: 600;
  padding-left: 35px;
  position: relative;
  transition: all 0.2s ease-in-out;
}

.form__switch .form__switch-toggle:after {
  content: '';
  height: 24px;
  width: 24px;
  border-radius: 100%;
  border: 1px solid #CCCCCC;
  background: white;
  position: absolute;
  margin: 0 3px;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  transition: all 0.2s ease-in-out;
}

.form__switch .form__switch-toggle--inline {
  margin-left: 15px;
}

.form__switch .form__switch-active-text, .form__switch .form__switch-inactive-text {
  overflow: hidden;
  display: block;
  margin: 0;
  transition: all 0.2s ease-in-out;
}

.form__switch .form__switch-active-text {
  display: none;
}

.form__switch input[type="checkbox"]:focus + label .form__switch-toggle {
  box-shadow: 0px 0px 10px #CCCCCC;
}

.form__switch input[type="checkbox"]:checked + label .form__switch-toggle {
  padding-left: 15px;
  padding-right: 35px;
  background: #47476b;
}

.form__switch input[type="checkbox"]:checked + label .form__switch-toggle:after {
  left: 100%;
  transform: translate(-33px, -50%);
}

.form__switch input[type="checkbox"]:checked + label .form__switch-active-text {
  display: block;
  color: white;
}

.form__switch input[type="checkbox"]:checked + label .form__switch-inactive-text {
  display: none;
}

.form__switch--inline {
  display: inline-block;
  margin-right: 30px;
}

