/* POPUP COMPONENTS
---------------------------------------------------------------------------*/
body.popup--open {
  overflow: hidden;
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
}

.popup {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  border: 1px solid #CCCCCC;
  z-index: 1500;
  height: auto;
  width: 750px;
  max-width: calc(100% - 15px * 2);
  max-height: calc(100% - 15px * 2);
  overflow: auto;
  padding-top: 30px;
}

.popup__close {
  position: absolute;
  top: 5px;
  right: 5px;
  background: transparent;
  border: none;
  cursor: pointer;
}

.popup__close + * {
  margin-top: 0;
}

.popup__overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.8);
  z-index: 1250;
}

.popup .scrolling-content {
  max-height: calc(100vh - 30px * 3 - 2px);
  padding: 30px;
  padding-top: 0;
  overflow: auto;
}

/* TAB COMPONENTS
---------------------------------------------------------------------------*/
.tabs {
  border: none;
}

.tabs__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: table;
  background: white;
  border-bottom: 1px solid #CCCCCC;
}

.tabs__list:after {
  clear: both;
  content: "";
  display: table;
}

.tabs__tab-control {
  margin: 0;
  float: left;
  text-align: center;
  display: block;
  padding: 10px 15px;
  transform: translate(0, 0);
  border-bottom: 3px solid transparent;
  margin-bottom: -2px;
}

.tabs__tab-control--active {
  border-bottom: 3px solid black;
}

.tabs__content {
  padding: 30px;
  background: white;
  display: none;
}

.tabs__content.active {
  display: block;
}

/* STATUS COMPONENTS
---------------------------------------------------------------------------*/
.status {
  padding-left: 25px;
  position: relative;
}

.status__icon {
  position: absolute;
  left: 0;
  top: 2px;
  color: #CCCCCC;
}

.status__bar {
  width: 100%;
  height: 20px;
  border: 1px solid #CCCCCC;
  border-radius: 3px;
  position: relative;
}

.status__bar:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 0%;
  background: #e62600;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  transition: all 0.4s ease-in-out;
}

.status__text {
  display: block;
  margin-top: 5px;
}

.status--25 .status__bar:before {
  width: 25%;
}

.status--50 .status__bar:before {
  width: 50%;
  background: #AF6C2D;
}

.status--75 .status__bar:before {
  width: 75%;
  background: #938F43;
}

.status--100 .status__icon {
  color: #77b259;
}

.status--100 .status__bar:before {
  width: 100%;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  background: #77b259;
}

code {
  display: block;
  padding: 1rem;
  word-wrap: normal;
  overflow: auto;
  background: #363738;
  color: #ef006b;
  font-family: monospace, monospace;
}

.code__prop {
  color: #6aff07;
}

.code__value {
  color: #fff72b;
}

.code__text {
  color: white;
}

*[parallax],
*[parallax-container] {
  position: relative;
  overflow: hidden;
}

.slideout {
  position: absolute;
  height: 100%;
  right: 0;
  top: 0;
  border-left: 1px solid #CCCCCC;
  width: 0;
  max-width: calc(100% - 47px);
  background: white;
  transition: width 0.4s ease-in-out;
}

.slideout.open {
  width: 400px;
  border: 1px solid #CCCCCC;
}

.slideout.open--wide {
  width: calc(100% - 47px);
}

.slideout--fixed {
  position: fixed;
  z-index: 100;
}

.slideout--left {
  right: auto;
  left: 0;
}

.slideout--left .slideout__controls {
  left: auto;
  right: 0;
  transform: translateX(100%);
}

.slideout__container {
  position: relative;
  padding-right: 60px;
  transition: padding 0.4s ease-in-out;
}

.slideout__container--left {
  padding-right: 0;
  padding-left: 60px;
}

.slideout__content-wrapper {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.slideout__content {
  display: none;
  padding: 30px;
  max-width: 1290px;
  margin: 0 auto;
}

.slideout__controls {
  margin: 0;
  list-style: none;
  padding: 0;
  position: absolute;
  left: 0;
  top: -1px;
  transform: translateX(-100%);
}

.slideout__controls li {
  margin: 0;
}

.slideout__controls li:last-of-type .slideout__toggle {
  border-bottom: 1px solid #CCCCCC;
}

.slideout__toggle {
  appearance: none;
  cursor: pointer;
  padding: 10px 15px;
  border: 1px solid #CCCCCC;
  border-bottom: none;
  font-size: 1.125em;
  font-weight: 400;
  line-height: 1.55556;
  transition: all 0.2s ease-in-out;
  background: white;
}

.slideout__toggle:hover, .slideout__toggle:focus {
  background: #CCCCCC;
  border-bottom: none;
}

@media (max-width: 767px) {
  .slideout.open {
    width: calc(100% - 47px);
  }
  .slideout--fixed {
    top: 90px;
  }
}

