/* NAVIGATION COMPONENTS
---------------------------------------------------------------------------*/
.nav {
  position: relative;
}

.nav:after {
  clear: both;
  content: "";
  display: table;
}

.nav__menu {
  padding: 0;
  margin: 0;
  list-style: none;
}

.nav__menu:last-child {
  margin: 0;
}

.nav__menu-item {
  margin: 0;
}

.nav__menu-link {
  display: flex;
  align-items: center;
  border: none;
}

.nav__menu-link i {
  margin-left: 5px;
}

.nav__menu-link.open .dropdown__icon {
  transform: rotate(180deg);
}

.nav__menu-link:hover, .nav__menu-link:focus {
  border: none;
}

.nav__menu-toggle {
  background: transparent;
  margin-left: 10px;
}

.nav__menu-toggle i {
  transition: transform 0.2s ease-in-out;
}

.nav__menu-toggle.open i {
  transform: rotate(180deg);
}

.nav__submenu {
  display: none;
}

.nav--horizontal .dropdown {
  position: static;
}

.nav--horizontal .nav__menu {
  float: left;
  display: flex;
  padding: 1px;
  outline: 1px solid #CCCCCC;
  outline-offset: -1px;
}

.nav--horizontal .nav__menu-item {
  display: flex;
  align-items: stretch;
  border-left: 1px solid #CCCCCC;
  margin: 0;
}

.nav--horizontal .nav__menu-item:first-of-type {
  border-left: none;
}

.nav--horizontal .nav__menu-item > .nav__menu {
  margin-top: 0;
  padding: 15px;
  padding-right: 0;
  outline: 0;
}

.nav--horizontal .nav__menu-link {
  border-bottom: none;
  padding: 15px;
  background: white;
  color: #b30077;
}

.nav--horizontal .nav__menu-link:hover, .nav--horizontal .nav__menu-link:focus {
  border-bottom: none;
}

.nav--horizontal .nav__wrapper {
  position: relative;
}

.nav--horizontal .nav__wrapper > .nav__menu-link {
  padding-right: 45px;
}

.nav--horizontal .nav__wrapper > .nav__menu-toggle {
  position: absolute;
  right: 0;
  padding: 15px;
  top: 50%;
  transform: translateY(-50%);
  height: 100%;
  background: transparent;
}

.nav--horizontal .nav__submenu {
  position: absolute;
  z-index: 10;
  width: 100%;
  top: 100%;
  left: 0;
  padding: 30px;
  border: 1px solid #CCCCCC;
  margin-top: -1px;
  background: white;
}

.nav--horizontal .nav__submenu .nav__menu {
  float: none;
  border: none;
  background: transparent;
  padding: 0;
  display: block;
  outline: none;
}

.nav--horizontal .nav__submenu .nav__menu-item {
  padding-left: 30px;
  margin-bottom: 15px;
  border: none;
  display: block;
}

.nav--horizontal .nav__submenu .nav__menu-item--first-level {
  padding-left: 0;
}

.nav--horizontal .nav__submenu .nav__menu-item--has-heading > .nav__menu > .nav__menu-item {
  padding-left: 0;
}

.nav--horizontal .nav__submenu .nav__menu-item + .nav__menu-item > h2 {
  margin-top: 45px;
}

.nav--horizontal .nav__submenu .nav__menu-item:last-of-type {
  margin-bottom: 0;
}

.nav--horizontal .nav__submenu .nav__menu-link {
  background: transparent;
  display: inline;
  padding: 0;
  margin: 0;
  text-decoration: none;
  border-bottom: 1px solid #b30077;
  transition: border 0.2s ease-in-out;
}

.nav--horizontal .nav__submenu .nav__menu-link:hover, .nav--horizontal .nav__submenu .nav__menu-link:focus {
  border-bottom: 1px solid transparent;
}

.nav--horizontal .nav__grid {
  margin: -30px;
  margin-bottom: 0;
}

.nav--horizontal .nav__col {
  padding: 30px;
  margin-bottom: -30px;
}

.nav--horizontal .nav__col:last-of-type {
  margin-bottom: -30px;
}

.nav--horizontal .nav__content {
  border-left: 1px solid #CCCCCC;
}

.nav--vertical .nav__menu-link {
  display: inline;
  padding: 0;
  margin: 0;
  text-decoration: none;
  border-bottom: 1px solid #b30077;
  transition: border 0.2s ease-in-out;
  background: transparent;
  color: #b30077;
}

.nav--vertical .nav__menu-link:hover, .nav--vertical .nav__menu-link:focus {
  border-bottom: 1px solid transparent;
}

.nav--vertical .nav__menu-item {
  margin-bottom: 15px;
}

.nav--vertical .nav__menu-item .nav__menu {
  padding: 15px;
  padding-bottom: 0;
}

.nav--vertical .nav__menu-item:last-of-type {
  margin-bottom: 0;
}

.mobile__toggle {
  margin: 0;
  position: fixed;
  right: 15px;
  top: 15px;
  height: 60px;
  z-index: 1100;
}

@media (max-width: 767px) {
  .nav--horizontal .nav__menu {
    display: block;
    width: 100%;
    outline: 0;
  }
  .nav--horizontal .nav__menu-item {
    display: block;
    border: none;
    border-bottom: 1px solid #CCCCCC;
  }
  .nav--horizontal .nav__menu-item:last-of-type {
    border-bottom: none;
  }
  .nav--horizontal .nav__menu-link {
    width: 100%;
  }
  .nav--horizontal .nav__submenu {
    margin-top: 0;
    position: static;
    border: none;
    border-top: 1px solid #CCCCCC;
  }
  .nav--horizontal .nav__submenu .nav__menu {
    float: none;
  }
  .nav--horizontal .nav__content {
    border: none;
  }
  .mobile__menu {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    padding-top: 90px;
    max-height: 100%;
    width: 100%;
    overflow-y: auto;
    background: white;
    border-bottom: 1px solid #CCCCCC;
  }
}

